import axios from 'axios';
// import url from './url';

const http = axios.create({
  // 
  timeout: 30000,
  // baseURL: 'http://eplusactivities-api:80'
  baseURL: 'http://localhost:52537'
  // baseURL: 'http://localhost:52538'
  // test
  // baseURL: 'https://www.ngs1685.com/'
  // baseURL: 'http://localhost:52537/eplusactivities'
  
  // axioshttps://www.kancloud.cn/yunye/axios/234845
});

// localstorage
// let token = JSON.parse(localStorage.getItem('token'))


// 
http.interceptors.request.use(
  config => {
    // 
    // :
    // if(localStorage.token) config.headers['token'] = localStorage.token;
    let token = JSON.parse(localStorage.getItem('token'))
    if (token) {
      config.headers.Authorization = 'Bearer ' + token.access_token
    }
    return config;
  },
  error => {
    console.log(error);
    return Promise.reject(error);
  }
);

http.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    return Promise.reject(error);
  }
);

export default http;
